"use client";
import { useState } from "react";
import {
  Home,
  Package,
  ShoppingCart,
  Box,
  Users,
  BarChart2,
  Tag,
  Truck,
  CreditCard,
  Settings,
  Brush,
  User,
  Bell,
  HelpCircle,
  LogOut,
  ChevronRight,
  ChevronDown,
} from "lucide-react";

export default function Sidebar() {
  const [isSidebarOpen, setSidebarOpen] = useState(true);
  const [activeSubmenu, setActiveSubmenu] = useState<string | null>(null);

  const toggleSubmenu = (menu: string) => {
    setActiveSubmenu(activeSubmenu === menu ? null : menu);
  };

  const menuItems = [
    {
      title: "Dashboard",
      submenu: ["Overview", "Sales Stats", "Order Summary"],
    },
    {
      title: "Orders",
      icon: ShoppingCart,
      submenu: ["All Orders", "Pending Orders", "Completed Orders", "Order History"],
    },
    {
      title: "Products",
      icon: Package,
      submenu: ["All Products", "Add New Product", "Product Categories", "Inventory Management"],
    },
    {
      title: "Customers",
      icon: Users,
      submenu: ["All Customers", "Add New Customer", "Customer Groups", "Customer Activity"],
    },
    {
      title: "Analytics/Reports",
      icon: BarChart2,
      submenu: ["Sales Reports", "Traffic Reports", "Product Performance", "Customer Reports"],
    },
    {
      title: "Categories",
      icon: Box,
      submenu: ["All Categories", "Add New Category", "Category Analytics"],
    },
    {
      title: "Coupons/Discounts",
      icon: Tag,
      submenu: ["Active Coupons", "Create New Coupon", "Expired Coupons", "Discount Campaigns"],
    },
    {
      title: "Shipping",
      icon: Truck,
      submenu: ["Shipping Methods", "Rates and Fees", "Shipping Labels", "Tracking"],
    },
    {
      title: "Payments",
      icon: CreditCard,
      submenu: ["Payment Gateways", "Transaction History", "Refunds", "Payment Settings"],
    },
    {
      title: "Settings",
      icon: Settings,
      submenu: ["Store Settings", "Payment Settings", "Tax Settings", "Shipping Settings"],
    },
    {
      title: "Theme Customization",
      icon: Brush,
      submenu: ["Change Theme", "Customize Colors", "Manage Fonts"],
    },
    { title: "User Profile", icon: User, submenu: null },
    { title: "Notifications", icon: Bell, submenu: null },
    {
      title: "Help & Support",
      icon: HelpCircle,
      submenu: ["Documentation", "Contact Support", "FAQ"],
    },
    { title: "Logout", icon: LogOut, submenu: null },
  ];

  return (
    <div className={`bg-white shadow-lg w-${isSidebarOpen ? "64" : "16"} h-screen transition-all`}>
      <div className="p-4">
        <ul className="space-y-4">
          {menuItems.map((item, index) => (
            <li key={index}>
              <div
                className={`flex items-center space-x-2 cursor-pointer ${
                  item.submenu ? "justify-between" : ""
                }`}
                onClick={() => item.submenu && toggleSubmenu(item.title)}
              >
                {item.icon && <item.icon className="w-5 h-5 text-gray-600" />}
                <span
                  className={`${
                    isSidebarOpen ? "block" : "hidden"
                  } text-gray-700 flex-grow`}
                >
                  {item.title}
                </span>
                {item.submenu && isSidebarOpen && (
                  <>
                    {activeSubmenu === item.title ? (
                      <ChevronDown className="w-4 h-4 text-gray-600" />
                    ) : (
                      <ChevronRight className="w-4 h-4 text-gray-600" />
                    )}
                  </>
                )}
              </div>
              {activeSubmenu === item.title && item.submenu && isSidebarOpen && (
                <ul className="pl-6 space-y-2 mt-2 text-sm text-gray-600">
                  {item.submenu.map((subItem, subIndex) => (
                    <li key={subIndex}>{subItem}</li>
                  ))}
                </ul>
              )}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
